.guide{
    margin-top: 0;
    padding-block: 48px 24px;
    .content-wrapper{
        .section-description{
            font-size: var(--title-font);
        }
    }
    .image-wrapper{
         img{
            max-width: 400px;
         }
    }
}
.using{
    padding-inline: 4% ;
    .using-content{
        width: 100%;
        overflow-x: auto;
        scrollbar-width: none;
        -ms-overflow-style: none;
        &:active{
            cursor: grabbing;
          }
        &::-webkit-scrollbar {
            display: none;
          }
        .guide-container{
            width: 100%;
            padding-block:48px;
            list-style: none;
             display: grid;
             grid-template-columns: repeat(4,1fr);
            .guide-card{
                width: 100%;
                min-width: 280px;
                text-align: center;
                display: flex;
                flex-direction: column;
                align-items: center;
                .photo{
                    width: 100%;
                    video{
                        max-width: 220px;
                        width: 100%;
                        height: 100%;
                        object-fit: contain;
                        border-radius: 24px;
                    }
                }
                .card-item{
                    span{
                        margin-inline: auto;
                        width: 32px;
                        height: 32px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        background-color: var(--main-color);
                        color: var(--background-color);
                        border-radius: 50%;
                    }
                    p{
                        margin-block: 12px;
    
                    }
                }
    
            }
        }
    }
   
}
