.guide {
  margin-top: 0;
  padding-block: 48px 24px;
}
.guide .content-wrapper .section-description {
  font-size: var(--title-font);
}
.guide .image-wrapper img {
  max-width: 400px;
}

.using {
  padding-inline: 4%;
}
.using .using-content {
  width: 100%;
  overflow-x: auto;
  scrollbar-width: none;
  -ms-overflow-style: none;
}
.using .using-content:active {
  cursor: grabbing;
}
.using .using-content::-webkit-scrollbar {
  display: none;
}
.using .using-content .guide-container {
  width: 100%;
  padding-block: 48px;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}
.using .using-content .guide-container .guide-card {
  width: 100%;
  min-width: 280px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.using .using-content .guide-container .guide-card .photo {
  width: 100%;
}
.using .using-content .guide-container .guide-card .photo video {
  max-width: 220px;
  width: 100%;
  height: 100%;
  -o-object-fit: contain;
     object-fit: contain;
  border-radius: 24px;
}
.using .using-content .guide-container .guide-card .card-item span {
  margin-inline: auto;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--main-color);
  color: var(--background-color);
  border-radius: 50%;
}
.using .using-content .guide-container .guide-card .card-item p {
  margin-block: 12px;
}

